const sendCard = (data) => {
  return fetch(EXPRESS_SERVER, {
    method: "POST",
    body: JSON.stringify(data),
    headers: { "Content-Type": "application/json" },
  })
    .then((response) => response.json())
    .then((data) => {
      return data;
    });
};

export default {
//  getCard: getCard,
  sendCard: sendCard,
};
